import React, { useEffect, useState } from 'react';
import Section from '../section';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';

const Contact = () => {
  const clientnameId = 'contact__formClientname';
  const emailId = 'contact__formEmail';
  const email2Id = 'contact__formEmail2';
  const addressId = 'contact__formAddress';
  const inquiryId = 'contact__formInquiry';

  const [forms, setForms] = useState({
    [clientnameId]: '',
    [emailId]: '',
    [email2Id]: '',
    [addressId]: '',
    [inquiryId]: ''
  });

  const [isInvalids, setIsInvalids] = useState({
    [emailId]: false,
    [email2Id]: false
  });

  const [disabledButton, setDisabledButton] = useState(true);

  const checkForm = () => {
    let isInvalidEmailId = false;
    let isInvalidEmail2Id = false;

    if (
      forms[emailId] &&
      forms[email2Id] &&
      forms[emailId] !== forms[email2Id]
    ) {
      isInvalidEmailId = true;
      isInvalidEmail2Id = true;
    }

    // フォームボタンが押せなくなる条件を記述
    setDisabledButton(
      !forms[clientnameId] ||
        !forms[emailId] ||
        !forms[email2Id] ||
        !forms[inquiryId] ||
        isInvalidEmailId ||
        isInvalidEmail2Id
    );

    setIsInvalids({
      ...isInvalids,
      [emailId]: isInvalidEmailId,
      [email2Id]: isInvalidEmail2Id
    });
  };

  useEffect(() => {
    checkForm();
  }, [forms]);

  const onChangeForm = (key, value) => {
    setForms({
      ...forms,
      [key]: value
    });
  };

  return (
    <Section id='contact'>
      <div className='contact-background'>
        <div className='row'>
          <div className='col-lg-8 mx-auto'>
            <p className='text-center mb-5'>
              不動産の購入、売却、賃貸管理、その他お気軽にお問い合わせください。
            </p>
            <form
              action='https://docs.google.com/forms/u/2/d/e/1FAIpQLSezATzl5zb4rVgOMdghXC1LhyhzXYIVaytaFpF0Qh4d_fi1_Q/formResponse'
              method='POST'
            >
              {[
                {
                  name: 'entry.1650788426',
                  id: clientnameId,
                  type: 'text',
                  required: true,
                  label: 'お名前',
                  example: '例：達磨 大志'
                },
                {
                  id: emailId,
                  type: 'email',
                  required: true,
                  label: 'メールアドレス',
                  example: '例：taishi@daruma-inv.com'
                },
                {
                  name: 'entry.1696363940',
                  id: email2Id,
                  type: 'email',
                  required: true,
                  label: 'メールアドレス',
                  description: '（確認用）',
                  example: '例：taishi@daruma-inv.com',
                  invalidMessage: 'メールアドレスが一致していません。',
                  invalidPaste: true
                },
                {
                  name: 'entry.1411341641',
                  id: addressId,
                  type: 'text',
                  label: 'ご住所',
                  example: '例：東京都渋谷区千駄ヶ谷2-10-5'
                },
                {
                  name: 'entry.881256958',
                  id: inquiryId,
                  required: true,
                  label: 'お問い合わせ内容',
                  example:
                    '※ 不動産の購入・売却、などなどお気軽にお問い合わせください。',
                  textarea: true,
                  rows: 3
                }
              ].map((d, i) => {
                const name = d.name || '';
                const description = d.description || '';
                const placeholder = d.placeholder || '';

                return (
                  <div className='form-group row' key={i}>
                    <label htmlFor={d.id} className='col-md-4 col-form-label'>
                      <Box display='flex' alignItems='center'>
                        <Box
                          mr={1}
                          className={`badge rounded-0 ${
                            d.required ? 'badge-danger' : 'badge-secondary'
                          }`}
                        >
                          {d.required ? '必須' : '任意'}
                        </Box>
                        <Typography>{d.label}</Typography>
                        {description && (
                          <Typography variant='body2' className='text-muted'>
                            {description}
                          </Typography>
                        )}
                      </Box>
                    </label>
                    <div className='col-md-8'>
                      {d.textarea ? (
                        <textarea
                          name={name}
                          className={`form-control form-control-lg ${
                            isInvalids[d.id] && 'is-invalid'
                          }`}
                          id={d.id}
                          rows={3}
                          required={!!d.required}
                          placeholder={placeholder}
                          value={forms[d.id]}
                          onChange={e => onChangeForm(d.id, e.target.value)}
                        ></textarea>
                      ) : (
                        <input
                          name={name}
                          className={`form-control form-control-lg ${
                            isInvalids[d.id] && 'is-invalid'
                          }`}
                          id={d.id}
                          type={d.type}
                          required={!!d.required}
                          placeholder={placeholder}
                          value={forms[d.id]}
                          onChange={e => onChangeForm(d.id, e.target.value)}
                          onPaste={e => {
                            if (d.invalidPaste) {
                              e.preventDefault();
                              return false;
                            }
                          }}
                        />
                      )}
                      {d.example && (
                        <Typography className='text-muted' variant='body2'>
                          {d.example}
                        </Typography>
                      )}
                      <div className='invalid-feedback'>{d.invalidMessage}</div>
                    </div>
                  </div>
                );
              })}
              <div className='form-group row'>
                <div className='col-sm-12'>
                  <button
                    type='submit'
                    className='btn btn-primary btn-block mt-4 py-3'
                    disabled={disabledButton}
                  >
                    <Typography>上記の内容で送信する</Typography>
                  </button>
                  <div className='text-muted text-center'>
                    <Typography my={4}>
                      ※上記フォームからの送信がうまくいかない場合は
                      <a
                        href='https://docs.google.com/forms/d/e/1FAIpQLSezATzl5zb4rVgOMdghXC1LhyhzXYIVaytaFpF0Qh4d_fi1_Q/viewform'
                        target='_blank'
                        rel='noopener noreferrer'
                        className='text-muted mx-1'
                        style={{
                          textDecoration: 'underline'
                        }}
                      >
                        こちらをクリック
                        <i className='fas fa-external-link-alt'></i>
                      </a>
                      してください。
                    </Typography>
                    <a
                      href='/privacy/'
                      className='text-muted'
                      style={{
                        textDecoration: 'underline'
                      }}
                    >
                      <Typography variant='body2'>
                        プライバシーポリシー
                      </Typography>
                    </a>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </Section>
  );
};

export default Contact;
